

import Vue from 'vue'
import FastClick from 'fastclick'
import VueRouter from 'vue-router'
import App from './App'
import axios from 'axios'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import LoadingPlugin from 'vux/src/plugins/loading/index.js'
import ConfirmPlugin from 'vux/src/plugins/confirm/index.js'
import AlertPlugin from 'vux/src/plugins/alert/index.js'



Vue.prototype.$baseUrl = 'http://lab.hlmr.ltd'




Vue.prototype.$wxUrl = 'https://open.weixin.qq.com/connect/oauth2/authorize' +
'?appid=wxcacab1dfed5bc106&redirect_uri=' + Vue.prototype.$baseUrl + '/wechat/index.html' +
'&response_type=code&scope=snsapi_base&state=STATE#wechat_redirect'
Vue.prototype.$axios = axios
Vue.use(VueRouter)

Vue.use(ToastPlugin)
Vue.use(LoadingPlugin)
Vue.use(ConfirmPlugin)
Vue.use(AlertPlugin)

const routes = [{
path: '/',
component: resolve => require(['./components/Index'], resolve)
}, {
path: '/lab',
component: resolve => require(['./components/Lab'], resolve),
meta: { requiresAuth: true }
}, {
path: '/register',
component: resolve => require(['./components/Register'], resolve)
}
]

const router = new VueRouter({
routes
})

router.beforeEach((to, from, next) => {
if (to.meta.requiresAuth) {  // 判断该路由是否需要登录权限
if (sessionStorage.getItem('token')) {  // 获取当前的token是否存在
next()
} else {
next({
path: '/',
query: { openid: sessionStorage.getItem('openid') }
})
}
} else {
if (to.matched.length === 0) { // 如果找不到此路由，则不跳转。

} else {
next()
}
}
})

FastClick.attach(document.body)

Vue.config.productionTip = false


axios.interceptors.request.use(config => {
Vue.$vux.loading.show({
text: 'loading'
})
const token = sessionStorage.getItem('token')
if (token) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
config.headers.Authorization = 'Bearer ' + token
}
config.headers.Accept = 'application/json'
return config
}, error => {
Vue.$vux.loading.hide()
return Promise.reject(error)
})


axios.interceptors.response.use(response => {
Vue.$vux.loading.hide()
return response
}, error => {
Vue.$vux.loading.hide()
if (error.response) {
switch (error.response.status) {
case 401:

sessionStorage.removeItem('token')
router.replace({
path: '/?openid=' + sessionStorage.getItem('openid'),
query: { redirect: router.currentRoute.fullPath }
})
break
case 403:
console.debug(error.response)
console.debug('无操作权限')
break
case 500:
Vue.$vux.toast.text(error.response.data)
}
} else {
console.debug('网络连接失败，请检查', error)
}
return Promise.reject(error)
})

Vue.prototype.getUrlKey = function (name) {
return decodeURIComponent((new RegExp('[?|&]' + name + '=' + '([^&;]+?)(&|#|;|$)').exec(location.href) || ['', ''])[1].replace(/\+/g, '%20')) || null
}

/* eslint-disable no-new */
new Vue({
router,
render: h => h(App)
}).$mount('#app-box')



